import {api} from '../../../application/Api';
import { ViewModel } from '../../../application/ViewModel';
import { TeamCreateModel } from '../models/TeamModels';

export default class TeamCreateViewModel extends ViewModel<TeamCreateModel> {
    constructor(props: any) {
        super(props);
    }

    handleChange(event: any) {
        super.handleChange(event);
        this.validate();
    }

    async validate(){
        await this.clearErrors();
        if (this.payload.teamName === '' || this.payload.teamName === undefined)
            await this.setError('teamName', 'Informe um nome para seu time')
    }
    
    async save() {
        await this.validate();
        
        if (!this.hasErrors()) {
            let data = {
                'ownerUuid': '3fa85f64-5717-4562-b3fc-2c963f66afa6',
                'name': this?.state.payload.teamName
            };

            api.post('/team', data)
                .then((response) => {
                    this.redirectTo('/team');

                })
                .catch((error) => {
                    console.log(error);
                    console.log('DEU ERRO');
                });
        }
    }
}